



















import Vue from "vue";
import config from "./config";

export default Vue.extend({
  props: {
    type: {
      type: String,
      default: "404"
    },
    title: {
      type: String,
      default: null
    },
    desc: {
      type: String,
      default: null
    },
    img: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      config
    };
  },
  computed: {
    pageType(): string {
      return this.type in this.config ? this.type : "404";
    }
  }
});
